'use client';

import React, { createContext, useContext, useState, useTransition } from 'react';

import { Loader2, WandSparklesIcon, Check, Copy, X } from 'lucide-react';
import { useEditorRef } from 'platejs/react';
import { toast } from 'sonner';

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { ToolbarButton } from '@/components/ui/toolbar';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';

// AI Provider Context - allows users to inject their own AI function
export type AskAIFunction = (command: string, context: string) => Promise<string | null>;

const AIContext = createContext<AskAIFunction | null>(null);

export function AIProvider({ 
  children, 
  askAI 
}: { 
  children: React.ReactNode; 
  askAI: AskAIFunction;
}) {
  return <AIContext.Provider value={askAI}>{children}</AIContext.Provider>;
}

export function useAI() {
  return useContext(AIContext);
}

export interface AIToolbarButtonProps {
  /** Custom AI function. If not provided, uses the AIContext */
  askAI?: AskAIFunction;
}

export function AIToolbarButton({ askAI: askAIProp }: AIToolbarButtonProps = {}) {
  const contextAskAI = useAI();
  const askAI = askAIProp ?? contextAskAI;
  const editor = useEditorRef();
  const [isPending, startTransition] = useTransition();
  const [generatedContent, setGeneratedContent] = useState('');
  const [showReviewDialog, setShowReviewDialog] = useState(false);
  const savedSelectionRef = React.useRef<any>(null);
  const [isFullContentMode, setIsFullContentMode] = useState(false);

  const handleAICommand = (command: string) => {
    // 
    savedSelectionRef.current = editor.selection;

    // 
    let context = '';
    let isFull = false;
    
    // 
    if (editor.selection) {
       // @ts-ignore: Dynamic access to editor.api
       const fragment = editor.api.string(editor.selection);
       if (fragment) {
         context = fragment;
       }
    } 
    
    // 
    if (!context) {
        //  Markdown API
        try {
          // PlateJS  Markdown 
          // @ts-ignore: Dynamic access to editor.api.markdown
          if (editor.api && editor.api.markdown && typeof editor.api.markdown.serialize === 'function') {
            // @ts-ignore: Dynamic access to editor.api.markdown
            context = editor.api.markdown.serialize();
          } else {
             // 
             context = (editor.children || []).map((n: any) => n.text || '').join('\n');
          }
        } catch (e) {
          console.warn('Failed to serialize markdown:', e);
        }
        
        isFull = true;
    }

    setIsFullContentMode(isFull);

    if (!context && command !== '') {
        toast.error('');
        return;
    }

    if (!askAI) {
        toast.error('AI  AIProvider  askAI prop  AI ');
        return;
    }

    startTransition(async () => {
      try {
        const result = await askAI(command, context);
        
        if (result) {
            setGeneratedContent(result);
            setShowReviewDialog(true);
        }
      } catch (error) {
        console.error(error);
        toast.error('AI ');
      }
    });
  };

  const handleApply = (mode: 'replace' | 'insert') => {
    // 
    // @ts-ignore: Dynamic call
    editor.tf.focus();
    
    //  Markdown 
    let nodes: any = null;
    try {
      // @ts-ignore: Dynamic access to editor.api.markdown
      if (editor.api && editor.api.markdown && typeof editor.api.markdown.deserialize === 'function') {
         // @ts-ignore: Dynamic access to editor.api.markdown
         nodes = editor.api.markdown.deserialize(generatedContent);
      }
    } catch (e) {
      console.warn('Failed to deserialize markdown:', e);
    }

    if (mode === 'replace') {
      if (isFullContentMode) {
          // 
          // @ts-ignore: Dynamic call
          editor.tf.select([]); 
          //  Plate/Slate select([]) 
          // 
          //  children insertText 
          // 
          if (editor.children.length > 0) {
             try {
                // @ts-ignore
                const start = editor.api.start([]);
                // @ts-ignore
                const end = editor.api.end([]);
                // @ts-ignore
                editor.tf.select({ anchor: start, focus: end });
             } catch (e) {
                // 
                // @ts-ignore
                editor.tf.select([]);
             }
          }
      } else if (savedSelectionRef.current) {
          // 
          // @ts-ignore: Dynamic call
          editor.tf.select(savedSelectionRef.current);
      }
      
      if (nodes) {
          // @ts-ignore: Dynamic call
          editor.insertFragment(nodes);
      } else {
          // @ts-ignore: Dynamic call
          editor.insertText(generatedContent);
      }
    } else {
      // 
      if (savedSelectionRef.current) {
          // 
           // @ts-ignore: Dynamic call
           editor.tf.select(savedSelectionRef.current);
           // 
           // @ts-ignore: Dynamic call
           editor.tf.collapse({ edge: 'end' });
      } else {
           // 
           // @ts-ignore: Dynamic call
           editor.tf.select([]);
           // @ts-ignore: Dynamic call
           editor.tf.collapse({ edge: 'end' });
      }
      
      // 
      // @ts-ignore: Dynamic call
      editor.insertText('\n\n');
      
      if (nodes) {
          // @ts-ignore: Dynamic call
          editor.insertFragment(nodes);
      } else {
          // @ts-ignore: Dynamic call
          editor.insertText(generatedContent);
      }
    }
    setShowReviewDialog(false);
    toast.success('');
  };

  return (
    <>
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <ToolbarButton tooltip="AI  (DeepSeek)" pressed={isPending}>
            {isPending ? (
              <Loader2 className="size-4 animate-spin text-purple-500" />
            ) : (
              <WandSparklesIcon className="size-4 text-purple-600 dark:text-purple-400" />
            )}
          </ToolbarButton>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start" className="w-56">
          <DropdownMenuLabel className="text-xs text-muted-foreground">
            DeepSeek AI 
          </DropdownMenuLabel>
          <DropdownMenuSeparator />
          <DropdownMenuItem onClick={() => handleAICommand('')}>
             
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => handleAICommand('')}>
             
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => handleAICommand('')}>
             
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => handleAICommand('')}>
             
          </DropdownMenuItem>
          <DropdownMenuSeparator />
          <DropdownMenuItem onClick={() => handleAICommand('')}>
             
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => handleAICommand('')}>
             
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>

      <Dialog open={showReviewDialog} onOpenChange={setShowReviewDialog}>
        <DialogContent className="max-w-2xl">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
               <WandSparklesIcon className="size-5 text-purple-600" />
               AI 
            </DialogTitle>
            <DialogDescription>
               AI 
            </DialogDescription>
          </DialogHeader>
          
          <div className="my-4 rounded-md border bg-muted/50 p-4">
            <ScrollArea className="h-[300px] w-full pr-4">
              <div className="whitespace-pre-wrap text-sm leading-relaxed">
                {generatedContent}
              </div>
            </ScrollArea>
          </div>

          <DialogFooter className="gap-2 sm:gap-0">
             <div className="flex w-full items-center justify-between">
                <Button variant="ghost" onClick={() => setShowReviewDialog(false)}>
                   
                </Button>
                <div className="flex gap-2">
                   <Button variant="outline" onClick={() => handleApply('insert')}>
                      <Copy className="mr-2 size-4" />
                      
                   </Button>
                   <Button onClick={() => handleApply('replace')}>
                      <Check className="mr-2 size-4" />
                      /
                   </Button>
                </div>
             </div>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
}
